<script setup>
const props = defineProps({
    modelValue: String
});

const emit = defineEmits(['update:modelValue']);

const updateValue = (event) => {
    emit('update:modelValue', event.target.value);
}

const clearValue = () => {
    emit('update:modelValue', '');
}
</script>
<template>
    <label for="search" class="label-search search-icon">
        <input type="text" placeholder="Поиск" id="search" class="search-input" :value="modelValue" @input="updateValue">
        <i v-show="modelValue" class="cross-icon" @click="clearValue"></i>
    </label>
</template>
<style scoped>
.search-input {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 108%;
    color: #000;
    height: 30px;
    padding: 0 27px 12px;
    border: none;
    outline: none;
    border-bottom: 1px solid #bfc9e0;
    width: 100%;
    max-width: 564px;
    transition: 0.3s;
}

.search-input::placeholder {
    font-style: italic;
    font-weight: 400;
    font-size: 15px;
    line-height: 108%;
    color: #8e9cbb;
}

.search-input:focus {
    border-color: #0066ff;
}

.label-search {
    position: relative;
}

.search-icon::before {
    content: url(data:image/svg+xml;base64,);
    position: absolute;
    top: 0;
    left: 0;
}

.cross-icon {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
}

.cross-icon::before {
    content: url(data:image/svg+xml;base64,);
}
</style>